.UserDialog-Wrapper{
    /*全屏*/
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    /*内容居中*/
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        background: hsla(0,0%,0%,0.4);
    }
    
    .UserDialog{
        border-radius: 4px;
        min-width: 20em;
        background: white;
        border:　1px solid #aaa;
        -webkit-box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
                box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
    }
    
    .UserDialog  .signInOrSignUp > nav:first-child{ 
        padding: 1em; 
        margin-left: -2em;
    }
    .UserDialog .signInOrSignUp  > nav:first-child label{ 
     
        margin-left: 2em; 
        cursor: pointer;
    }
    
    .UserDialog .signInOrSignUp > .panes { 
        padding: 0 1em 1em; 
    }
    
    .UserDialog form>.row{ 
        display: -ms-flexbox; 
        display: flex; padding: .3em 0; 
    }
    .UserDialog form>.row>label:first-child{ 
        width: 4em; 
    }
    .UserDialog form>.row.actions{
         padding-left: 4em; display: -ms-flexbox; display: flex; -ms-flex-align: center; align-items: center;
    }
    .UserDialog form>.row.actions button{
         margin-right: .5em; 
    }
    
    .UserDialog button{
        font: inherit; padding: .2em;
    }

    /* .UserDialog .forgotPassword >h3{
        padding: 1em;
    }

    .UserDialog .forgotPassword > form{
        padding: 0 1em 1em;
    } */
    .UserDialog .forgotPassword > h3 { padding: 1em; }
    .UserDialog .forgotPassword > form { padding: 0 1em 1em; }
    	


